  position: relative;\n        width: calc(100% - 400px);\n        background-size: cover;\n        background-position: center bottom;\n        height: 100vh;\n        &:before {\n            position: absolute;\n            background-color: rgba($color-overlay, .1);\n            height: 100%;\n            width: 100%;\n            display: block;\n            content: \"\";\n        }\n        .header {\n            z-index: 2;\n            display: flex;\n            align-items: center;\n            width: 100%;\n            padding: 60px 60px 0;\n            height: 100px;\n            .menu {\n                margin-left: auto;\n                display: flex;\n                li {\n                    display: inline;\n                    margin: 0 10px;\n                    a {\n                        color: #ffffff;\n                        text-decoration: none;\n                        cursor: pointer;\n                        font-weight: 500;\n                        text-transform: uppercase;\n                        font-size: 12px;\n                        //background-image: linear-gradient(to right, #f6d365 0%, #fda085 51%, #f6d365 100%);\n                        text-shadow: 1px 2px 4px $color-primary;\n                        padding: 10px 12px;\n                        border-radius: 10px;\n                    }\n                }\n            }\n        }\n        .title {\n            position: absolute;\n            left: 80px;\n            display: flex;\n            flex-direction: column;\n            justify-content: center;\n            align-items: flex-start;\n            width: 400px;\n            height: 400px;\n            z-index: 10;\n            h2 {\n                font-family: 'ToyotaType-Semibold', sans-serif;\n                max-width: 400px;\n                font-size: 28px;\n                margin-bottom: 10px;\n                line-height: 38px;\n                text-transform: uppercase;\n                text-align: left;\n                letter-spacing: .5px;\n                color: rgba(#ffffff, 1);\n                text-shadow: 1px 2px 4px rgba($color-primary, 1);\n            }\n            p {\n                color: $color-primary;\n                text-transform: uppercase;\n                font-size: 16px;\n                letter-spacing: .5px;\n                font-family: 'ToyotaType-Regular', sans-serif;\n            }\n        }\n        .footer {\n            z-index: 2;\n            position: absolute;\n            bottom: 40px;\n            right: 60px;\n            display: flex;\n            flex-direction: column;\n            h3 {\n                color: #fff;\n                font-size: 15px;\n                font-weight: 500;\n                text-align: left;\n                padding: 0 0 10px 10px;\n            }\n            .box-row.app {\n                display: flex;\n                margin: 0 auto;\n                a {\n                    border: solid 1px #ffffff;\n                    border-radius: 10px;\n                    padding: 5px 15px;\n                    display: -webkit-inline-box;\n                    display: -ms-inline-flexbox;\n                    display: inline-flex;\n                    -webkit-box-align: center;\n                    -ms-flex-align: center;\n                    align-items: center;\n                    -webkit-box-pack: center;\n                    -ms-flex-pack: center;\n                    justify-content: center;\n                    text-transform: uppercase;\n                    text-decoration: none;\n                    color: #414752;\n                    margin: 0px 10px 10px;\n                    -webkit-transition: all 0.4s;\n                    transition: all 0.4s;\n                    font-size: 14px;\n                    background: rgba(#fff, 0.3);\n                    img {\n                        height: 34px;\n                        margin-right: 10px;\n                    }\n                 