ore,\n*:after {\n    margin: 0;\n    padding: 0;\n    box-sizing: inherit;\n}\n\n/* BODY */\n\nbody {\n    position: relative;\n    color: #666;\n    font: 16px/26px \"Roboto\", sans-serif !important;\n    text-align: center;\n    height: 100%;\n    overflow: hidden;\n}\n\n@media only screen and (min-width: 308px) {\n}\n\n.fill {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n}\n\n.login {\n    margin: 0;\n    width: 100%;\n    height: 100%;\n    min-height: 100vh;\n    display: flex;\n    background-size: cover;\n    background-position: center top;\n    z-index: 1;\n\n    .content {\n        flex: 1;\n        display: flex;\n        position: relative;\n        height: 100vh;\n        width: 60%;\n        @media (max-width: 768px) {\n            width: 0%;\n        }\n\n        .bg-layer {\n            position: absolute;\n            height: 100%;\n            width: 100%;\n            background: rgba(#4b6584, .5);\n            z-index: 10;\n            top: 0;\n            bottom: 0;\n            left: 0;\n            right: 0;\n        }\n\n        .slider {\n            position: absolute;\n            top: 0;\n            bottom: 0;\n            left: 0;\n            right: 0;\n            background-color: #4b6584;\n            height: 100vh;\n            z-index: 1;\n\n            .ivu-carousel {\n                height: 100vh;\n                width: 100%;\n\n                .ivu-carousel-list {\n                    height: 100vh;\n                }\n\n                .ivu-carousel-track {\n                    height: 100vh;\n                }\n\n                .ivu-carousel-item {\n                    height: 100vh !important;\n                }\n            }\n\n            .ivu-carousel-dots {\n                bottom: 50px !important;\n                text-align: left;\n                padding-left: 60px;\n\n                li button {\n                    background-color: #ffffff !important;\n                    height: 6px;\n                    border-radius: 5px;\n                }\n\n                li.ivu-carousel-active > button {\n                    width: 36px !important;\n                }\n            }\n\n            .ivu-carousel-active > button {\n                background-color: $color-primary !important;\n            }\n\n            .bg-cover {\n                height: 100%;\n                width: 100%;\n                background-position: center !important;\n                background-size: cover !important;\n            }\n        }\n\n        .header {\n            z-index: 11;\n            display: flex;\n            align-items: center;\n            width: 100%;\n            padding: 60px 60px 0;\n            height: 100px;\n\n            .logo {\n                flex: 1;\n                display: flex;\n                justify-content: flex-start;\n\n                img {\n                    height: 70px;\n                }\n            }\n\n            .menu {\n                margin-left: auto;\n                display: flex;\n\n                li {\n                    display: inline;\n                    margin: 0 10px;\n\n                    a {\n                        color: #ffffff;\n                        text-decoration: none;\n                        cursor: pointer;\n                        font-weight: 500;\n                        text-transform: uppercase;\n                        font-size: 12px;\n                    }\n                }\n            }\n        }\n\n        .footer {\n            z-index: 2;\n            position: absolute;\n            bottom: 40px;\n            right: 60px;\n            display: flex;\n            flex-direction: column;\n\n            h3 {\n                color: #fff;\n                font-size: 15px;\n                font-weight: 500;\n                text-align: left;\n                padding: 0 0 10px 10px;\n            }\n        }\n    }\n\n    .auth {\n        box-shadow: -2px 0px 5px 1px rgba(#222, 0.1);\n        background: #ffffff;\n        position: relative;\n        z-index: 0;\n        width: 40%;\n        height: 100vh;\n        overflow: auto;\n        margin-left: auto;\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        z-index: 2;\n        @media (max-width: 768px) {\n            width: 100%;\n            .form-wrap {\n                width: 100%;\n            }\n        }\n\n        .logo {\n            width: 100%;\n            text-align: left;\n            height: 100px;\n\n            img {\n                height: 60px;\n            }\n\n            &.with-text {\n                display: flex;\n                width: 100%;\n                align-items: center;\n\n                img {\n                    height: 60px;\n                    width: auto !important;\n                    margin-right: 20px;\n                }\n\n                span {\n                    font-size: 12px;\n                    font-weight: 700;\n                    line-height: 20px;\n                    text-transform: uppercase;\n                    max-width: 220px;\n                }\n            }\n        }\n    }\n}\n\n.form-wrap {\n    width: 70%;\n\n    .form-content {\n        padding: 0 3em;\n        text-align: left;\n        width: auto;\n\n        h2 {\n            font-size: 22px;\n            margin: 0 0 5px;\n            padding: 0 0 0 10px;\n            color: $color-primary;\n            text-transform: uppercase;\n            font-weight: 600;\n            border-left: solid 3px $color-primary;\n        }\n\n        h5 {\n            font-size: 12px;\n            font-weight: 400;\n            line-height: 14px;\n            margin-bottom: 40px;\n        }\n\n        #msg {\n            .error {\n                color: $color-danger;\n            }\n\n            .success {\n                color: $color-success;\n            }\n\n            font-size: 12px;\n            text-align: center;\n            margin-top: 20px;\n            transition: 0.25s ease-in-out;\n        }\n\n        form {\n            min-width: 300px !important;\n\n            .input {\n                position: relative;\n                margin: 0 0 0.8em 0;\n\n                .icon {\n                    width: 16px;\n                    height: 16px;\n                    background: url(../../../images/icon.png) no-repeat -3px -4px;\n                    position: absolute;\n                    left: 6px;\n                    top: 8px;\n                    opacity: 0.6;\n                    cursor: text;\n                    z-index: 10;\n                    display: block;\n                    transition: 0.25s ease-in-out;\n\n                    &.pass {\n                        background: url(../../../images/icon.png) no-repeat -3px -83px;\n                    }\n                }\n\n                input {\n                    padding: 8px 10px 8px 28px;\n                    font-size: 14px;\n                    width: 100%;\n                    display: inline-block;\n                    background: transparent;\n                    border: 0;\n                    border-bottom: 2px solid #c2cfdc;\n                    outline: 0;\n                    box-sizing: border-box;\n                    -moz-box-sizing: border-box;\n                    font-family: inherit;\n                    color: #666666;\n                    transition: 0.25s ease-in-out;\n                    margin-bottom: 10px;\n\n                    &:disabled {\n                        opacity: 0.5;\n\n                        &:hover,\n                        &:active,\n                        &:focus {\n                            border-bottom: 2px solid #c2cfdc;\n                        }\n                    }\n\n                    &:active,\n                    &:focus {\n                        border-bottom: 2px solid $color-primary;\n\n                        & + span {\n                            opacity: 1;\n                        }\n                    }\n\n                    &::placeholder {\n                        /* Chrome, Firefox, Opera, Safari 10.1+ */\n                        color: #999999;\n                    }\n                }\n            }\n\n            .button {\n                margin: 1em 0;\n                height: 40px;\n                width: 50%;\n                display: inline-flex;\n                align-items: center;\n                justify-content: center;\n                background-color: $color-primary;\n                border: none;\n                color: #fff;\n                font-size: 16px;\n                border-radius: 30px;\n                box-shadow: none;\n                outline: none;\n                font-weight: 300;\n                transition: 0.25s ease-in-out;\n                position: relative;\n                cursor: pointer;\n\n                span.loader {\n                    position: absolute;\n                    right: 5px;\n                }\n\n                &:hover {\n                    background-color: darken($color-primary, 5%);\n                }\n            }\n\n            .action {\n                padding: 1.2em 0 0;\n                font-size: 0.93em;\n\n                p {\n                    float: right;\n                    text-align: left;\n                    font-size: 13px;\n                    margin-top: 25px;\n\n                    a {\n                        color: $color-primary;\n                        text-decoration: underline;\n                        font-size: 14px;\n\n                        &:hover {\n                            color: darken($color-primary, 5%);\n                            text-decoration: underline;\n                        }\n                    }\n                }\n            }\n\n            .checkbox {\n                margin: 1em 0;\n                padding: 20px 0;\n                visibility: hidden;\n                text-align: left;\n\n                &:checked + label:after {\n                    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)\";\n                    filter: alpha(opacity=100);\n                    opacity: 1;\n                }\n            }\n\n            label[for] {\n                position: relative;\n                padding-top: 2px;\n                padding-left: 10px;\n                cursor: pointer;\n                font-size: 13px;\n                border-radius: 2px;\n\n                &:before {\n                    position: absolute;\n                    width: 17px;\n                    height: 17px;\n                    top: 0px;\n                    left: -14px;\n                    content: \"\";\n                    border: 1px solid #5a6374;\n                }\n\n                &:after {\n                    position: absolute;\n                    top: 1px;\n                    left: -10px;\n                    width: 15px;\n                    height: 8px;\n                    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)\";\n                    filter: alpha(opacity=0);\n                    opacity: 0;\n                    content: \"\";\n                    background-color: transparent;\n                    border: solid $color-primary;\n                    border-width: 0 0 2px 2px;\n                    -webkit-transform: rotate(-45deg);\n                    -moz-transform: rotate(-45deg);\n                    -o-transform: rotate(-45deg);\n                    transform: rotate(-45deg);\n                }\n            }\n        }\n    }\n\n    .copyright {\n        position: absolute;\n        bottom: 24px;\n        font-size: 12px;\n        color: #666666;\n        width: 100%;\n        text-align: left;\n        padding-left: 50px;\n    }\n}\n\n@media (max-width: 768px) {\n    .content {\n        display: none !important;\n    }\n\n    .auth {\n        .logo {\n            top: 10px;\n            left: 10px;\n            width: 40px;\n            height: 40px;\n            background: none;\n\n            img {\n                width: 100%;\n            }\n        }\n\n        padding-top: 0;\n        float: left;\n        width: 100%;\n        height: 500px;\n        border: 0;\n    }\n}\n\n.d-modal {\n    display: flex;\n    flex-direction: column;\n    position: relative;\n    height: 100%;\n\n    .modal-header {\n        height: 50px;\n        display: flex;\n        align-items: center;\n        border-bottom: dotted 1px $color-primary;\n        padding: 0 0 0 20px;\n\n        .title {\n            flex: 1;\n            font-size: 18px;\n            color: $color-primary;\n            text-transform: uppercase;\n        }\n\n        .close {\n            display: inline-flex;\n            align-items: center;\n            justify-content: center;\n            margin-left: auto;\n            width: 50px;\n            height: 50px;\n            cursor: pointer;\n            color: $color-primary !important;\n\n            i {\n                color: $color-primary !important;\n                font-size: 40px;\n            }\n        }\n    }\n\n    .modal-body {\n        height: calc(100% - 50px);\n        overflow-y: auto;\n        padding: 20px 40px;\n        position: relative;\n\n        iframe {\n            border: 0 !important;\n        }\n\n        &.np {\n            padding: 0 !important;\n        }\n\n        .ivu-collapse {\n            border-top: 0;\n            border-bottom: 0;\n\n            .ivu-collapse-item {\n                .ivu-collapse-header {\n                    height: 44px;\n                    color: $color-primary;\n                    line-height: 44px;\n                }\n\n                &:last-child {\n                    border-bottom: 0 !important;\n                }\n            }\n        }\n    }\n}\n\n.mapouter {\n    position: relative;\n    height: 300px;\n    width: 100%;\n    background-color: #4b6584;\n}\n\n.gmap_canvas {\n    overflow: hidden;\n    display: block;\n    background: none !important;\n    height: 300px;\n    width: 100%;\n    background-color: #4b6584;\n}\n\n.contact-info {\n    padding: 40px 40px;\n\n    ul {\n        list-style: none;\n\n        li {\n            display: flex;\n            max-width: 500px;\n            margin-bottom: 20px;\n            justify-content: flex-start;\n\n            i {\n                color: $color-primary;\n                font-size: 28px;\n                margin-right: 20px;\n            }\n\n            span {\n                font-size: 16px;\n                text-align: left;\n            }\n        }\n    }\n}\n\n.news-list {\n    list-style: none;\n\n    li {\n        display: flex;\n        margin-bottom: 20px;\n        padding-bottom: 20px;\n        width: 100%;\n        border-bottom: solid 1px #e5e5e5;\n\n        &:last-child {\n            border-bottom: 0;\n        }\n\n        img {\n            width: 100px;\n            height: auto;\n            border-radius: 5px;\n            //   border: solid 1px $color-primary;\n            margin-right: 20px;\n        }\n\n        .info {\n            display: flex;\n            flex-direction: column;\n            flex: 1;\n            justify-content: flex-start;\n            align-items: flex-start;\n\n            h3 {\n                font-size: 15px;\n                font-weight: 500;\n                position: relative;\n                margin-bottom: 5px;\n\n                &::after {\n                    content: \" \";\n                    position: absolute;\n                    width: 20px;\n                    left: 0;\n                    bottom: 0;\n                    display: block;\n                    height: 1px;\n                    background-color: $color-primary;\n                }\n            }\n\n            &-sub {\n                display: flex;\n                align-items: center;\n                margin-bottom: 10px;\n\n                i {\n                    color: $color-primary;\n                    margin-right: 5px;\n                    font-size: 18px;\n                }\n\n                .dt {\n                    font-size: 12px;\n                }\n            }\n\n            a {\n                border: solid 1px $color-primary;\n                font-size: 12px;\n                margin-top: 15px;\n                color: $color-primary;\n                padding: 3px 15px;\n            }\n        }\n    }\n}\n\n.news-detail {\n    font-size: 18px;\n\n    h2 {\n        position: relative;\n        margin-bottom: 20px;\n        font-size: 18px;\n        display: flex;\n        align-items: center;\n\n        &::after {\n            content: \" \";\n            position: absolute;\n            width: 20px;\n            left: 40px;\n            bottom: 0;\n            display: block;\n            height: 1px;\n            background-color: $color-primary;\n        }\n\n        a {\n            color: $color-primary;\n            border: solid 1px $color-primary;\n            border-radius: 50%;\n            display: inline-flex;\n            align-items: center;\n            justify-content: center;\n            margin-right: 10px;\n            width: 30px;\n            height: 30px;\n            cursor: pointer;\n        }\n    }\n\n    img {\n        width: 100%;\n        border: solid 1px $color-primary;\n        border-radius: 10px;\n    }\n}\n\n.box-row.app {\n    display: flex;\n    margin: 0 auto;\n\n    a {\n        border: solid 1px #ffffff;\n        border-radius: 10px;\n        padding: 5px 15px;\n        display: -webkit-inline-box;\n        display: -ms-inline-flexbox;\n        display: inline-flex;\n        -webkit-box-align: center;\n        -ms-flex-align: center;\n        align-items: center;\n        -webkit-box-pack: center;\n        -ms-flex-pack: center;\n        justify-content: center;\n        text-transform: uppercase;\n        text-decoration: none;\